<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-text="返回" left-arrow @click-left="goShopbag" />
    <div class="order-bgimg"></div>
    <div class="tab-box">
      <van-tabs v-model="tabIndex" color="#0C45A0" title-active-color="#0C45A0" @change="toggleStatus">
        <van-tab :title="v.title" v-for="(v,i) in tabItems" :key="i">
         <div v-if="orderDatas.length > 0">
            <div class="tab-item" v-for="(item,index) in orderDatas" :key="index">
            <div class="tab-item-box clearfix">
              <div class="fl">
                <div class="orderID">{{item.orderId}}</div>
              </div>
              <div class="fr">
                <div class="text" v-if="item.status == 1">
                  <div @click="confirmReceipt(item,index)">确认收货</div>
                </div>
                <div class="text-box-item" v-else>
                  <div class="finished fl">已完成</div>
                  <div class="remove fr" @click="removeOrder(item.orderId,index)">
                    <van-icon name="delete" />
                  </div>
                </div>
              </div>
            </div>

            <div class="order-box">
              <div class="order-content">
                <van-swipe-cell>
                  <div class="clearfix" v-for="(v,i) in item.products" :key="i">
                    <div class="order-img fl">
                      <img class="auto-img" :src="v.smallImg" alt />
                    </div>
                    <div class="order-info fl">
                      <div class="clearfix">
                        <div class="text-box fl">
                          <div class="text-name">{{v.name}}</div>
                          <div class="enname">{{v.enname}}</div>
                          <div class="text-rule">{{v.rule}}</div>
                        </div>
                        <div class="price-box fr">
                          <div class="price">￥{{v.price}}</div>
                          <div class="count">x {{v.count}}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </van-swipe-cell>
                <div class="pro-box">
                  <div class="time">{{item.data | formatData('yyyy-MM-dd')}}</div>
                  <div class="clearfix pro-boxing">
                    <div class="fl pro-count">共计{{item.count}} 件商品</div>
                    <div class="fr pro-total">合计 ￥{{item.total}}</div>
                  </div>
                  <div class="left-box left"></div>
                  <div class="left-box right"></div>
                </div>
              </div>
            </div>
          </div>
         </div>

         <div v-else>
           <van-empty description="订单数据空空如也，去逛一逛!">
              <van-button round color="#0C34BA" class="bottom-button" @click="goMenu">
                去逛一逛
              </van-button>
            </van-empty>
         </div>
        </van-tab>

      </van-tabs>
    </div>
  </div>
</template>

<script>
import { utils } from "../../assets/js/utils";
export default {

  data() {
    return {
      tabIndex: 0,
      orderDatas: [],
      tabItems: [
        {title:'全部'},
         {title:'进行中'},
          {title:'已完成'},
      ]
    };
  },
  created() {
    // 查询订单数据
    this.findOrder(0);
  },
 
  methods: {
    //返回上一级
    returnLast() {
      this.$router.go(-1);
    },

      //返回购物车
    goShopbag(){
      this.$router.push({name:'Shopbag'})
    },

    //前往菜单
    goMenu(){
      this.$router.push({name:'Menu'})
    },

    //查询订单数据
    findOrder(status) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status
        }
      })
        .then(result => {
          this.orderDatas = [];
          this.$toast.clear();
          

          if (result.data.code == 70000) {
            let orderData = {};

            result.data.result.map(v => {
              if (!orderData[v.oid]) {
                orderData[v.oid] = {
                  orderId: v.oid,
                  data: v.createdAt,
                  count: v.count,
                  total: v.count * v.price,
                  products: [v],
                  status: v.status
                };
              } else {
                orderData[v.oid].count += v.count;
                orderData[v.oid].total += v.count * v.price;
                orderData[v.oid].products.push(v);
              }
            });

            for (let key in orderData) {
              this.orderDatas.push(orderData[key]);
            }

            this.orderDatas.sort((a, b) => {
              return new Date(b.data).getTime() - new Date(a.data).getTime();
            });

          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //切换订单状态
    toggleStatus(name) {
      this.findOrder(name);
    },
    //确认收货
    confirmReceipt(item,index) {
      
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oid:item.orderId
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "POST",
        url: "/receive",
        data
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 80000) {
        
            this.$toast(result.data.msg);
            if (this.tabIndex == 1) {
              this.orderDatas.splice(index,1);
            }else{
              item.status = 2;
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //删除订单
    removeOrder(oid,index) {
   
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oid
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "POST",
        url: "/removeOrder",
        data
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 90000) {
         
            this.$toast(result.data.msg);
            this.orderDatas.splice(index,1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
 
  }
};
</script>

<style lang="less" scoped>
/deep/.van-nav-bar__text {
  color: #0c34ba;
}
/deep/ .van-nav-bar .van-icon {
  color: #0c34ba;
}

.order-bgimg {
  height: 80px;
  background-color: #0c34ba;
}
.tab-item {
  margin: 10px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 20px;
}
.tab-item-box {
  font-size: 14px;
  color: #0c34ba;
  font-weight: bold;
}

.tab-box {
  margin-bottom: 50px;
}
.order-box {
  text-align: left;
  .order-img {
    width: 80px;
    height: 80px;
    background-color: #999;
  }
  .order-info {
    margin-left: 10px;
    width: calc(~"100% - 90px");
    line-height: 20px;
    margin-top: 20px;
    .text-name {
      color: #0c34ba;
      font-size: 16px;
      font-weight: bold;
    }
    .price-box {
      color: #0c34ba;
      font-size: 16px;
      text-align: right;
      font-weight: bold;

      .count {
        margin-top: 20px;
      }
    }
  }
}
.pro-box {
  margin-top: 15px;
  border-top: 1px dashed black;
  position: relative;

  .pro-boxing {
    font-size: 15px;
    color: #0c34ba;
    font-weight: bold;
  }
}
.time {
  margin: 10px 0;
  color: #0c34ba;
  font-size: 14px;
}
.left-box {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: -10px;
}

.left {
  left: -30px;
}
.right {
  right: -30px;
}
.remove {
  margin-top: 3px;
  margin-left: 3px;
}
</style>